import "@babel/polyfill";
require('intersection-observer');
import "../scss/main.scss";

import '../img/favicon.ico';
import '../img/icon.png';

import * as text3d from './3dtext';

import './color-animation';
import { positionSlider } from './positionSlider';


window.addEventListener('load', function () {
    var navs = document.querySelectorAll(".js-nav");
    var workItem = document.querySelectorAll(".work__item");
    var scrollNav = document.querySelectorAll(".work__nav-link");
    var merch = document.querySelectorAll(".merch-photo");
    var workNav = document.querySelector(".nav__work");
    var slider = document.querySelector(".work__slider");
    var media = document.querySelectorAll(".work__media");
    var scrollInProgress = false;
    let mobileLandscape = window.matchMedia("(max-width: 767px) and (orientation: landscape)");
    let mobilePortrait = window.matchMedia("(max-width: 599px) and (orientation: portrait)");

    // synchronize sliders position on orientation change
    positionSlider(slider);

    //navigation

    navs.forEach(function (el) {
        el.addEventListener('change', function () {

            var prev = document.querySelector(".active");
            var current = document.querySelector("." + el.dataset.id);

            if (prev && current) {
                if (prev.className !== current.className) {
                    prev.classList.toggle("active");
                    current.classList.toggle("active");
                    //reset slider
                    if (el.dataset.id == "work") {
                        TweenLite.to(slider, 0, { scrollTo: "#work1" });
                    }
                }

            }

            //stop 3d animation when switching to other page

            if (el.dataset.id != "home") {
                text3d.stopAnimation();
            }
            else if (prev != current) {
                text3d.stopAnimation();
                text3d.idleAnimation();
            }
        });
    });

    //start animation on mobile landscape

    mobileLandscape.addListener(landscapeHandler);
    mobilePortrait.addListener(portraitHandler);

    function landscapeHandler(evt) {
        if (evt.matches) {
            text3d.stopAnimation();
            text3d.idleAnimation();
        }
    }

    function portraitHandler(evt) {
        let current = document.querySelector(".active");
        if (evt.matches && !current.classList.contains("home")) {
            text3d.stopAnimation();
        }
    }


    //scroll to first slide

    workNav.addEventListener('click', () => {
        var active = document.querySelector(".active");
        if (active.classList.contains("work")) {
            scrollNav[0].click();
        }
    })

    scrollNav.forEach(el => {
        el.addEventListener('click', e => {
            if (TweenLite) {
                e.preventDefault();
                scrollInProgress = true;
                document.querySelector(".scrolled").classList.remove("scrolled");
                el.classList.add("scrolled");
                TweenLite.to(slider, 1, { scrollTo: "#" + el.href.split("#")[1], onComplete: () => scrollInProgress = false });
            }
        });
    });


    //observe items showing in viewport in wrk section

    const intersectionObserver = new IntersectionObserver((entries, observer) => {
        entries.forEach((entry) => {
            if (!scrollInProgress) {
                if (entry.intersectionRatio > 0.6) {
                    scrollNav.forEach(nav => {
                        nav.classList.remove("scrolled");
                        if (entry.target.id == nav.href.split("#")[1]) {
                            nav.classList.add("scrolled");
                        }
                    })

                }
            }

        });
    }, { threshold: 0.6 });

    workItem.forEach((element) => intersectionObserver.observe(element));


    //gallery show hide 
    media.forEach((mediaEl) => {

        let imgs = mediaEl.querySelectorAll(".work__img");
        imgs.forEach((el, index) => {
            el.addEventListener('click', e => {
                el.style.opacity = '0';
                el.style.zIndex = '-1';
                imgs[index + 1 >= imgs.length ? 0 : index + 1].style.opacity = '1';
                imgs[index + 1 >= imgs.length ? 0 : index + 1].style.zIndex = '1';
            });
        })


    });

});

document.addEventListener("DOMContentLoaded", function () {
    let lazyBackgrounds = [].slice.call(document.querySelectorAll(".lazy-background"));
    let slider = document.querySelector(".work__slider");

    if ("IntersectionObserver" in window) {
        let lazyBackgroundObserver = new IntersectionObserver(function (entries, observer) {
            entries.forEach(function (entry) {
                if (entry.isIntersecting) {
                    entry.target.classList.add("visible");
                    lazyBackgroundObserver.unobserve(entry.target);
                }
            });
        }, { root: slider });

        lazyBackgrounds.forEach(function (lazyBackground) {
            lazyBackgroundObserver.observe(lazyBackground);
        });
    }
});