export function positionSlider(slider) {
    let noChangeCountToEnd = 100,
        noEndTimeout = 1000,
        prevOffset = 0;

    function doOnOrientationChange() {
        prevOffset = Number.parseFloat(slider.scrollTop / slider.scrollHeight).toFixed(2);
        slider.style.opacity = "0";

        let interval,
            timeout,
            end,
            lastInnerWidth,
            lastInnerHeight,
            noChangeCount;

        end = function () {
            clearInterval(interval);
            clearTimeout(timeout);

            interval = null;
            timeout = null;

            switch (window.orientation) {
                case 90:
                case -90:
                case 0:
                    preserveSliderPosition();
            }
        };

        interval = setInterval(function () {
            if (window.innerWidth === lastInnerWidth && window.innerHeight === lastInnerHeight) {
                noChangeCount++;

                if (noChangeCount === noChangeCountToEnd) {
                    // The interval resolved the issue first.

                    end();
                }
            } else {
                lastInnerWidth = window.innerWidth;
                lastInnerHeight = window.innerHeight;
                noChangeCount = 0;
            }
        });
        timeout = setTimeout(function () {
            // The timeout happened first.

            end();
        }, noEndTimeout);



    }

    window.addEventListener('orientationchange', doOnOrientationChange);


    function preserveSliderPosition() {
        let offset = (slider.scrollHeight * parseFloat(prevOffset));
        TweenLite.to(slider, 0, { scrollTo: offset });
        slider.style.opacity = "1";

    }
}

